.btn {
	position: relative;
	display: block;
	padding: 33.5px 0;

	font-size: 28px;
	font-weight: 800;
	letter-spacing: 0.05em;
	text-align: center;
	color: #ffffff;
	text-decoration: none;

	background: linear-gradient(-97.73deg, #FFC653 4.94%, #FF6600 95.06%);
	box-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
	text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
	border-radius: 20px;
	z-index: 1;
	&:before {
		content: "";
		visibility: visible;
		opacity: 1;
		transition: 0.3s;

		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background: linear-gradient(-97.73deg, #FFC653 4.94%, #FF6600 95.06%);
		border-radius: 20px;
		z-index: -1; }

	&:after {
		content: "";
		visibility: hidden;
		opacity: 0;
		transition: 0.3s;

		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background: linear-gradient(97.73deg, #FFC653 4.94%, #FF6600 95.06%);
		border-radius: 20px;
		z-index: -1; }

	@include r(900) {
		font-size: 21px;
		padding: 17px 0; }

	@include r(768) {
		border-radius: 10px;
		&:after, &:before {
			border-radius: 10px; } }

