	&--less {
		font-size: 14px;
		padding: 28px 0;
		&:before {
			content: "";
			visibility: visible;
			opacity: 1;
			transition: 0.3s;

			position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			background: linear-gradient(97.73deg, #FFC653 4.94%, #FF6600 95.06%);
			border-radius: 20px;
			z-index: -1; }

		&:after {
			content: "";
			visibility: hidden;
			opacity: 0;
			transition: 0.3s;

			position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			background: linear-gradient(-97.73deg, #FFC653 4.94%, #FF6600 95.06%);
			border-radius: 20px;
			z-index: -1; }
		@include r(900) {
			font-size: 14px; }
		@include r(768) {
			padding: 9px;
			letter-spacing: 0.05em;
			&:after, &:before {
				border-radius: 10px; } } }

	&:hover {
		&:before {
			visibility: hidden;
			opacity: 0; } }

	&:hover {
		&:after {
			visibility: visible;
			opacity: 1; } } }



