.time-block {
	padding: 45px 0;
	margin-bottom: 50px;

	&__title {
		margin-bottom: 15px;
		text-align: center; }

	&__subtitle {
		max-width: 700px;
		margin: 0 auto 35px;
		text-align: center;
		line-height: 1.3; }

	&__data {
		width: 560px;
		margin: 0 auto;
		@include r(768) {
			max-width: calc(100% - 24px); } } }
