.question-block {
	position: relative;
	padding: 50px 0;
	margin-bottom: 50px;
	@include r(768) {
		padding: 32px 0 37px; }

	&__title {
		text-align: center;
		margin-bottom: 22px; }

	&__num {
		position: absolute;
		top: -32px;
		left: 70px;

		font-size: 57px;
		font-weight: 800;
		color: $orange;
		@include r(720) {
			top: -23px;
			left: 50%;
			transform: translateX(-50%);
			font-size: 43px; } }

	&__board {
		position: relative;
		left: -50px;

		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		width: 956px;
		padding: 50px 100px;

		background: #FFFFFF;
		box-shadow: 0px 5px 50px rgba(6, 19, 51, 0.25);
		border-radius: 10px;
		@include r(1044) {
			width: 850px; }
		@include r(900) {
			width: 800px; }
		@include r(899) {
			width: 110%;
			left: -5%; }
		@include r(800) {
			width: 104%;
			left: -2%; }
		@include r(720) {
			width: calc(100% + 50px);
			left: -25px;
			background-color: transparent;
			box-shadow: none;
			padding: 0; } }

	&__item {
		position: relative;
		width: 295px;
		height: 215px;

		background-size: cover;
		background-repeat: no-repeat;
		// background-position: center center

		margin-right: 20px;
		margin-bottom: 20px;
		&:nth-child(2n) {
			margin-right: 0; }
		&:nth-child(n+3) {
			margin-bottom: 0; }

		cursor: pointer;
		&:hover .question-block__bg {
			opacity: 0.5; }
		&:hover .question-block__text {
			opacity: 1; }

		@include r(900) {
			margin-right: 10px;
			margin-bottom: 10px;
			&:nth-child(2n) {
				margin-right: 0; }
			&:nth-child(n+3) {
				margin-bottom: 0; } }
		@include r(899) {
			@include css-lock(240,276,720,899,width);
			@include css-lock(186,215,420,899,height); }
		@include r(720) {
			width: calc(50% - 5px); } }


	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(-126.09deg, rgba(255, 202, 85, 0.5) 0%, #FF5623 100%);
		opacity: 0;
		transition: 0.5s;
		@include r(768) {
			opacity: 0.5; } }

	&__text {
		opacity: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 260px;
		font-size: 18px;
		font-weight: 800;
		text-align: center;
		text-shadow: 0px 3px 5px rgba(6, 19, 51, 0.5);
		z-index: 5;
		transition: 0.5s;
		@include r(1024) {
			opacity: 1;
			top: 70%;
			width: 75%; } } }
