.btns-bottom {
	display: flex;
	justify-content: center;
	padding: 50px 0;
	margin-bottom: 50px;
	@include r(768) {
		flex-direction: column;
		padding: 0; }

	&__item {
		width: 252px;
		margin-right: 50px;
		text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
		&:last-child {
			margin-right: 0; }

		@include r(768) {
			width: 275px;
			position: relative;
			top: -10px;
			margin: 0 auto;
			&:last-child {
				margin-right: auto;
				top: auto;
				bottom: -10px; } } } }
