.thank-content {
	position: relative;
	top: 70px;
	display: flex;
	flex-direction: column;
	width: 956px;
	min-height: 550px;
	margin: 0 auto;
	padding: 50px 25px 50px 634px;

	background-color: #ffffff;
	box-shadow: 0px 5px 50px rgba(6, 19, 51, 0.25);
	border-radius: 10px;
	@include r(1400) {
		top: 20px; }
	@include r(1080) {
		width: 850px;
		padding-left: 530px; }
	@include r(970) {
		width: calc(100% - 50px); }
	@include r(900) {
		@include css-lock(400, 550, 768, 900, min-height);
		@include css-lock(445, 525, 768, 900, padding-left); }
	@include r(768) {
		top: 0;
		max-width: 324px;
		padding: 41px 24px 30px; }

	&__title {
		max-width: 300px;
		margin-top: 50px;
		margin-bottom: 15px;

		font-size: 28px;
		font-weight: 800;
		text-transform: uppercase;
		color: $orange;
		word-break: break-word;
		@include r(900) {
			margin-top: 25px;
			margin-bottom: 20px; }
		@include r(768) {
			font-size: 18px;
			margin-top: 0;
			margin-bottom: 15px; } }

	&__subtitle {
		max-width: 260px;
		font-size: 18px;
		line-height: 1.4;
		font-weight: 800;
		color: #223055;
		@include r(900) {
			margin-bottom: 60px; }
		@include r(768) {
			font-size: 14px;
			margin-bottom: 20px; } }

	&__media {
		position: absolute;
		left: -50px;
		display: flex;
		flex-shrink: 0;
		width: 634px;
		height: 450px;
		border-radius: 5px;
		overflow: hidden;
		@include r(1080) {
			width: 530px; }
		@include r(970) {
			@include css-lock(430, 530, 768, 970, width);
			left: -15px; }
		@include r(900) {
			@include css-lock(300, 450, 768, 900, height); }
		@include r(768) {
			position: relative;
			left: -33px;
			width: 343px;
			height: 208px; } }

	&__btn {
		width: 370px;
		margin-top: auto;
		@include r(970) {
			width: 290px; }
		@include r(768) {
			position: relative;
			top: -30px;
			width: 275px; } }

	&__text {
		max-width: 300px;
		margin-top: 16px;
		font-size: 14px;
		font-weight: 300;
		color: #223055;
		@include r(970) {
			max-width: 270px; }
		@include r(768) {
			font-size: 12px;
			margin-top: -5px; }
		&--dark {
			margin-top: 0;
			margin-bottom: 20px;
			font-weight: 500;
			color: #162447;
			letter-spacing: 0.05em;
			@include r(768) {
				margin-bottom: 12px; } } } }
