.promo {
	position: relative;
	padding-top: 120px;
	padding-bottom: 50px;
	margin-bottom: 50px;
	@include r(900) {
		padding-bottom: 25px; }
	@include r(768) {
		padding: 88px 22px 0; }

	&__ask {
		position: absolute;
		top: 80px;
		left: 45px;

		transform: rotate(-16deg);
		font-size: 18px;
		font-weight: 800;
		text-transform: uppercase;
		@include r(768) {
			font-size: 14px;
			top: 33px;
			left: auto;
			right: 10px; } }


	&__top {
		margin-left: 535px;
		margin-bottom: 50px;
		@include r(1044) {
			margin-left: 482px; }
		@include r(900) {
			margin-left: 428px; }
		@include r(768) {
			margin: 0;
			margin-bottom: 5px;
			h3 {
				font-size: 18px; } } }

	&__title {
		position: absolute;
		top: 151px;
		left: 50%;
		transform: translateX(-50%);
		display: flex;
		flex-direction: column;

		z-index: 10;
		h3 {
			text-align: right; }
		@include r(768) {
			width: 375px;
			text-align: center;
			position: relative;
			top: -9px;
			& h2 {
				font-size: 39px; }
			& h3 {
				font-size: 18px;
				text-align: left;
				margin-left: 48px;
				z-index: 10; } } }

	&__main-title {
		margin-bottom: 10px;

		font-size: 70px;
		line-height: 1.1;
		text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
		text-transform: uppercase;
		@include r(900) {
			font-size: 52px; }
		@include r(768) {
			z-index: 10; } }

	&__subtitle {
		width: 450px;
		z-index: 10;
		margin-top: -34px;
		span {
			display: inline-block;
			font-size: 57px;
			color: #FF6600; }
		@include r(900) {
			width: 390px; }
		@include r(768) {
			margin-top: 0;
			line-height: 1.5;
			span {
				font-size: 46px;
				line-height: 0; } } }

	&__board {
		width: 956px;
		position: relative;
		left: -50px;

		padding: 45px 500px 30px 100px;
		margin-bottom: 45px;

		background: linear-gradient(93.56deg, #FFC653 4.03%, #FF6600 95.97%);
		box-shadow: 0px 5px 50px rgba(6, 19, 51, 0.25);
		border-radius: 10px;
		@include r(1044) {
			width: 850px;
			padding: 45px 490px 30px 100px; }
		@include r(900) {
			width: 730px;
			left: -15px;
			padding: 45px 200px 30px 100px; }
		@include r(768) {
			width: calc(100% + 64px);
			left: -32px;
			top: -60px;
			padding: 70px 32px 30px;
			margin-bottom: -30px; } }

	&__text {
		font-size: 14px;
		color: #223055; }

	&__wrapper {
		display: flex;
		margin-left: 50px;
		margin-bottom: 85px;
		@include r(768) {
			flex-direction: row-reverse;
			margin: 0;
			margin-bottom: 190px; } }

	&__logo {
		margin-right: 90px;
		@include r(768) {
			position: relative;
			right: -32px;
			margin-right: 0; } }

	&__img {
		position: absolute;
		bottom: 0;
		right: -26px;
		display: flex;
		@include r(900) {
			right: -35px;
			img {
				height: 271px;
				width: auto; } } }

	&__btn {
		width: 304px;
		margin-left: 50px;
		@include r(768) {
			position: relative;
			z-index: 20;
			bottom: 0;
			transform: translateY(50%);
			margin: 0;
			width: auto;
			max-width: 300px; } } }
