.btn {
  position: relative;
  display: block;
  padding: 33.5px 0;
  font-size: 28px;
  font-weight: 800;
  letter-spacing: 0.05em;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  background: linear-gradient(-97.73deg, #FFC653 4.94%, #FF6600 95.06%);
  box-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
  text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
  border-radius: 20px;
  z-index: 1;
}

.btn:before {
  content: "";
  visibility: visible;
  opacity: 1;
  transition: 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(-97.73deg, #FFC653 4.94%, #FF6600 95.06%);
  border-radius: 20px;
  z-index: -1;
}

.btn:after {
  content: "";
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(97.73deg, #FFC653 4.94%, #FF6600 95.06%);
  border-radius: 20px;
  z-index: -1;
}

@media only screen and (max-width: 900px) {
  .btn {
    font-size: 21px;
    padding: 17px 0;
  }
}

@media only screen and (max-width: 768px) {
  .btn {
    border-radius: 10px;
  }
  .btn:after, .btn:before {
    border-radius: 10px;
  }
}

.btn--less {
  font-size: 14px;
  padding: 28px 0;
}

.btn--less:before {
  content: "";
  visibility: visible;
  opacity: 1;
  transition: 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(97.73deg, #FFC653 4.94%, #FF6600 95.06%);
  border-radius: 20px;
  z-index: -1;
}

.btn--less:after {
  content: "";
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(-97.73deg, #FFC653 4.94%, #FF6600 95.06%);
  border-radius: 20px;
  z-index: -1;
}

@media only screen and (max-width: 900px) {
  .btn--less {
    font-size: 14px;
  }
}

@media only screen and (max-width: 768px) {
  .btn--less {
    padding: 9px;
    letter-spacing: 0.05em;
  }
  .btn--less:after, .btn--less:before {
    border-radius: 10px;
  }
}

.btn:hover:before {
  visibility: hidden;
  opacity: 0;
}

.btn:hover:after {
  visibility: visible;
  opacity: 1;
}

