body {
	padding-top: 50px;
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	line-height: 1.2;
	letter-spacing: 0.1em;
	color: #ffffff;
	background-color: #dde1e8;
	overflow-x: hidden; }

.container {
	position: relative;
	width: 856px;
	margin: 0 auto;
	@include r(1044) {
		width: 750px; }
	@include r(900) {
		width: 700px; }
	@include r(768) {
		width: 100%;
		padding: 0 25px; } }

.section {
	background: linear-gradient(230.73deg, #223055 0%, #061333 100%);
	border-radius: 10px; }

.title {
	font-size: 28px;
	font-weight: 800;
	text-transform: uppercase;
	@include r(900) {
		font-size: 21px; }

	&--orange {
		color: $orange; }

	&--thin {
		font-weight: 500; } }

.logo {
	display: flex;
	img {
		@include css-lock(120,200,320,900,width);
		@include css-lock(30,50,320,900,height); } }

.main {

	&__top {
		position: relative; }

	&__bottom {
		position: relative;
		width: 856px;
		margin: 0 auto;
		@include r(1044) {
			width: 750px; }
		@include r(970) {
			width: calc(100% - 50px); }
		@include r(768) {
			max-width: 324px; }
		&--other {
			@include r(768) {
				width: 100%;
				max-width: 100%; }
			a {
				bottom: 27px; } } }

	&__link {
		display: block;
		position: absolute;
		bottom: -102px;
		left: -50px;

		font-size: 14px;
		font-weight: 800;
		text-transform: uppercase;
		text-decoration: none;
		color: #101D40;
		opacity: 0.5;
		transition: 0.5s;
		@include r(1400) {
			bottom: -52px; }
		@include r(970) {
			left: 0; }
		@include r(768) {
			bottom: -22px; }
		&:hover {
			opacity: 1; } }

	&__back {
		display: none;
		&.is-active {
			display: block; }
		a {
			left: 0; }

		@include r(970) {
			width: 100%; } }

	&__logo {
		position: absolute;
		left: -250px;
		top: 0; }

	&__btn {
		position: absolute;
		right: -225px;
		top: 0;
		a {
			font-size: 14px;
			font-weight: 800;
			text-transform: uppercase;
			text-decoration: none;
			color: #101D40;
			opacity: 0.5;
			transition: 0.5s; }

		&:hover a {
			opacity: 1; } }

	@include r(1400) {
		&__top {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 30px; }

		&__logo {
			position: static; }

		&__btn {
			position: static; } }
	@include r(768) {
		margin-bottom: 20px;
		&__top {
			margin-bottom: 20px; } } }


