.btns-block {
	display: flex;
	justify-content: space-between;
	align-items: center;

	width: 956px;
	position: relative;
	left: -50px;
	@include r(1044) {
		width: 850px; }
	@include r(900) {
		flex-wrap: wrap;
		width: 730px;
		left: -15px; }
	@include r(768) {
		width: calc(100% + 20px);
		left: -10px; }

	&__cloud {
		position: absolute;
		top: -417px;
		left: auto;
		right: -75px;
		transform: translateX(0);

		@include r(1200) {
			width: 150px;
			height: 115px;
			top: -370px;
			right: -29px; }
		@include r(1044) {
			top: -315px;
			right: -51px;
			transform: rotate(22deg); }
		@include r(1000) {
			width: 95px;
			top: -276px;
			height: 75px;
			right: 0px; }
		@include r(900) {
			width: 95px;
			top: -37px;
			height: 75px;
			right: auto;
			left: 107px; }
		@include r(768) {
			top: -48px;
			left: 110px; } }

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;

		width: 200px;
		height: 150px;
		padding: 0 20px;

		color: #162447;
		text-align: center;
		text-decoration: none;

		background: linear-gradient(-107.03deg, #FFC653 8.81%, #FF6600 91.19%);
		box-shadow: 0px 5px 50px rgba(6, 19, 51, 0.25);
		border-radius: 10px;
		z-index: 1;
		@include r(768) {
			height: 140px;
			&:last-child {
				margin-bottom: 0; } }

		&:hover {
			&:before {
				visibility: hidden;
				opacity: 0; } }
		&:hover {
			&:after {
				visibility: visible;
				opacity: 1; } }
		&:before {
			content: "";
			visibility: visible;
			opacity: 1;
			transition: 0.3s;

			position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			background: linear-gradient(-107.03deg, #FFC653 8.81%, #FF6600 91.19%);
			border-radius: 10px;
			z-index: -1; }

		&:after {
			content: "";
			visibility: hidden;
			opacity: 0;
			transition: 0.3s;

			position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			background: linear-gradient(107.03deg, #FFC653 8.81%, #FF6600 91.19%);
			border-radius: 10px;
			z-index: -1; }

		span {
			position: absolute;
			bottom: -30px;
			right: 10px;

			font-size: 57px;
			font-weight: 800;
			color: #FF6600;
			text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.25);
			@include r(768) {
				font-size: 43px;
				bottom: -20px;
				right: 30px; } }

		@include r(1044) {
			width: 180px; }

		@include r(900) {
			width: 350px;
			margin-bottom: 30px;
			padding-left: 160px; }

		@include r(768) {
			width: 100%;

			&--no-img {
				padding-left: 0; } }

		@include r(600) {
			padding: 0 18px 0 184px;
			text-align: left;
			justify-content: left;
			&--no-img {
				padding: 0 140px 0 84px; } } }

	&__img {
		position: absolute;
		top: -261px;
		left: 50%;
		transform: translateX(-50%);

		@include r(1044) {
			height: 220px;
			width: auto;
			top: -220px; }

		@include r(900) {
			height: 160px;
			top: auto;
			bottom: 0;
			left: 30px;
			transform: translateX(0); } } }






