.time {
	display: flex;
	justify-content: space-between;

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include r(768) {
			width: 110px; } }

	&__num {
		font-size: 57px;
		font-weight: 800;
		color: #FF6600;
		text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
		@include r(900) {
			font-size: 46px; } }

	&__text {
		font-size: 28px;
		font-weight: 800;
		text-transform: uppercase;
		@include r(900) {
			font-size: 18px; } } }
