.winners {
	padding: 50px 0;
	margin-bottom: 50px;
	@include r(768) {
		padding: 25px 0 35px; }

	&__inner {
		margin-bottom: 50px;
		@include r(900) {
			margin-bottom: 35px; }
		@include r(768) {
			&:last-child {
				margin-bottom: 0; } } }

	&__title {
		text-align: center;
		@include r(900) {
			margin-bottom: 30px; }
		@include r(768) {
			width: 80%;
			margin: 0 auto;
			margin-bottom: 30px; } }

	&__wrapper {
		width: 956px;
		position: relative;
		left: -50px;
		@include r(1044) {
			width: 850px; }
		@include r(900) {
			width: 700px;
			left: auto; }
		@include r(768) {
			width: calc(100% + 20px);
			left: -10px; } }

	&__inner {} }

.winner {
	position: relative;
	display: flex;
	align-items: center;
	@include r(900) {
		flex-direction: column; }

	&__board {
		position: relative;
		right: 20px;

		flex-shrink: 0;

		width: 775px;
		padding: 24px 70px;

		background: linear-gradient(-93.36deg, #FFC653 4%, #FF6600 96%);
		box-shadow: 0px 5px 50px rgba(6, 19, 51, 0.25);
		border-radius: 5px;

		z-index: 10;
		@include r(1044) {
			width: 670px; }
		@include r(900) {
			right: auto;
			z-index: 0;
			top: -15px;
			text-align: center;
			background: linear-gradient(93.36deg, #FFC653 4%, #FF6600 96%); }
		@include r(768) {
			padding: 41px 0 16px;
			width: 100%;
			z-index: 5; } }

	&__photo {
		position: relative;
		width: 200px;
		height: 190px;
		flex-shrink: 0;
		background-size: cover;
		background-repeat: no-repeat;
		border-radius: 5px;
		@include r(900) {
			z-index: 10; }
		@include r(768) {
			width: 158px;
			height: 150px; } }

	&__name {
		margin-bottom: 5px;
		font-size: 28px;
		color: $darkblue;
		font-weight: 800;
		text-transform: uppercase;
		@include r(900) {
			font-size: 21px; } }

	&__num {
		position: absolute;
		bottom: -27px;
		left: 50%;
		transform: translateX(-50%);

		font-size: 57px;
		line-height: 1;
		font-weight: 800;
		color: $orange;
		text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
		@include r(900) {
			bottom: -10px;
			left: -160px; }
		@include r(768) {
			font-size: 43px;
			bottom: -4px;
			left: -50px; } }


	&__date {
		font-size: 14px;
		color: #162447; }

	&__img {
		position: absolute;
		right: -74px;
		bottom: 0;
		@include r(1024) {
			right: -20px;
			top: 120px; }
		@include r(768) {
			right: 24px;
			top: auto;
			bottom: -30px;
			img {
				width: 125px;
				height: 85px; } } } }

