.info-block {
	padding: 50px 0;
	margin-bottom: 50px;
	@include r(768) {
		padding: 30px 0 0; }

	&__board {
		width: 956px;
		position: relative;
		left: -50px;

		padding: 45px 0 30px;
		margin-bottom: 25px;

		background: linear-gradient(-94.69deg, #FFC653 4.21%, #FF6600 95.79%);
		box-shadow: 0px 5px 50px rgba(6, 19, 51, 0.25);
		border-radius: 10px;
		@include r(1044) {
			width: 850px; }
		@include r(900) {
			width: 730px;
			left: -15px; }
		@include r(768) {
			padding: 25px 0 20px;
			width: calc(100% + 20px);
			left: -10px; } }

	&__title {
		text-align: center;
		margin-bottom: 15px;
		padding: 0 15px; }

	&__subtitle {
		padding: 0 15px;
		font-size: 57px;
		text-align: center;
		text-shadow: 0px 5px 10px rgba(6, 19, 51, 0.3);
		@include r(900) {
			font-size: 43px; }
		@include r(768) {
			line-height: 1.18; } }

	&__text {
		text-align: center;
		letter-spacing: 0;
		margin-bottom: 65px;
		@include r(900) {
			margin-bottom: 35px; }
		@include r(768) {
			margin-bottom: 40px;
			&--no-marg {
				margin-bottom: 0;
				letter-spacing: 0.05em; } } }

	&__promo {
		width: 550px;
		margin: 0 auto 80px;
		text-align: center;
		span {
			display: inline-block;
			font-size: 57px;
			color: #FF6600; }
		@include r(900) {
			margin: 0 auto 50px; }
		@include r(768) {
			width: auto;
			max-width: 275px;
			span {
				font-size: 46px;
				line-height: 0; } } }

	&__caption {
		text-align: center;
		margin-bottom: 20px; }

	&__wrapper {
		max-width: 600px;
		padding: 0 20px;
		margin: 0 auto;
		line-height: 1.4; }

	&__buttons {
		margin-bottom: 60px;
		@include r(900) {
			margin-bottom: 40px; } }

	&__btn {
		max-width: 300px;
		margin: 0 auto;
		@include r(768) {
			max-width: 275px;
			position: relative;
			bottom: -25px; } } }

